* {
  box-sizing: border-box;
}

html {
  font-family: Roboto, sans-serif;
}

body, h1, p {
  margin: 0;
}

a {
  background-color: #0000;
  text-decoration: none;
}

ul, ol {
  margin: 0;
  padding: 0;
  list-style: none;
}

img {
  border-style: none;
}

button {
  padding-block: 0;
  padding-inline: 0;
  font-family: inherit;
}

.card {
  background-color: #fff;
  border: 1px solid #f3f3f3;
  border-radius: 5px;
  flex-direction: column;
  width: 100%;
  max-width: 200px;
  padding: 16px;
  font-size: 12px;
  line-height: 18px;
  transition: transform .3s linear;
  display: flex;
}

.card:hover {
  transform: scale(1.2);
}

.card__img {
  max-width: 160px;
  height: 134px;
  margin: 16px auto 0;
  display: block;
}

.card__info {
  margin-top: 40px;
}

.card__title {
  color: #060b35;
  cursor: pointer;
  font-size: 12px;
  font-weight: 500;
  line-height: 18px;
  transition: color .3s linear;
}

.card:hover .card__title {
  color: #34568b;
}

.card__subtitle {
  color: #616070;
  margin-top: 4px;
  font-size: 10px;
  line-height: 14px;
}

.card__rating {
  justify-content: space-between;
  align-items: center;
  margin-top: 16px;
  display: flex;
}

.card__reviews {
  color: #060b35;
  font-size: 10px;
  line-height: 14px;
}

.card__price {
  color: #616070;
  justify-content: space-between;
  align-items: center;
  margin-top: 24px;
  font-size: 12px;
  line-height: 18px;
  display: flex;
}

.card__btn {
  text-align: center;
  color: #fff;
  text-transform: uppercase;
  cursor: pointer;
  background-color: #00acdc;
  border: 1px solid #00acdc;
  border-radius: 5px;
  height: 40px;
  margin-top: 16px;
  font-size: 14px;
  font-weight: 700;
  line-height: 16px;
  text-decoration: none;
  transition: color .3s linear, background-color .3s linear;
}

.card__btn:hover {
  color: #00acdc;
  background-color: #fff;
}

.value {
  color: #060b35;
  text-align: right;
  font-size: 16px;
  font-weight: 700;
  line-height: 18px;
}

.stars {
  justify-content: center;
  align-items: center;
  display: flex;
}

.stars__star {
  background-image: url("star.822d561e.svg");
  background-position: center;
  background-repeat: no-repeat;
  width: 16px;
  height: 16px;
  margin-right: 4px;
}

.stars__star:nth-child(-n+4) {
  background-image: url("star-active.edda4c43.svg");
}

.header {
  justify-content: space-between;
  align-items: center;
  margin: 0 auto;
  padding: 0 50px;
  display: flex;
  box-shadow: 0 2px 4px #0000000d;
}

.logo {
  height: 40px;
}

.nav__items {
  justify-content: space-between;
  align-items: center;
  height: 100%;
  display: flex;
}

.nav__item {
  height: 100%;
  margin-right: 20px;
  position: relative;
}

.nav__item:last-child {
  margin-right: 0;
}

.nav__link {
  color: #060b35;
  text-transform: uppercase;
  height: 100%;
  font-size: 12px;
  font-style: normal;
  font-weight: 500;
  line-height: 60px;
  transition: color .3s linear;
  display: block;
}

.nav__link:hover, .is-active {
  color: #00acdc;
}

.is-active:after {
  content: "";
  background-color: #00acdc;
  border-radius: 8px;
  width: 100%;
  height: 4px;
  display: block;
  position: absolute;
  bottom: 0;
  left: 0;
}

.main {
  grid-template-rows: 1fr 1fr;
  grid-template-columns: repeat(auto-fit, 200px);
  justify-content: center;
  gap: 46px 48px;
  margin: 0 auto;
  padding: 50px 40px;
  display: grid;
}

@media (width >= 488px) {
  .main {
    grid-template-columns: repeat(2, 200px);
  }
}

@media (width >= 768px) {
  .main {
    grid-template-columns: repeat(3, 200px);
  }
}

@media (width >= 1024px) {
  .main {
    grid-template-columns: repeat(4, 200px);
  }
}

